@page {
  size: auto;
  margin: 1cm;
}

:root {
  --font-family: "Segoe UI Variable", "Segoe UI", system-ui, sans-serif;
  --bg-color: #f3f3f3;
  --content-bg-color: #fff;
  --text-color: #1d1d1d;
  --text-color-secondary: #606060;
  --border-color: #e1e1e1;
  --shadow-color: #00000014;
  --header-bg: #f9f9f9;
  --accent-color: #0078d4;
  --accent-color-dark: #005a9e;
  --accent-bg-light: #e7f3ff;
  --button-bg: #fff;
  --button-border: #d1d1d1;
  --button-hover-bg: #f9f9f9;
  --table-hover-bg: #f5faff;
  --delete-color: #d13438;
  --delete-hover-bg: #fdf0f0;
  --disabled-bg: #f9f9f9;
  --disabled-color: #a1a1a1;
  --disabled-border: #e1e1e1;
}

body.dark-mode {
  --bg-color: #1c1c1c;
  --content-bg-color: #282828;
  --text-color: #f0f0f0;
  --text-color-secondary: #a0a0a0;
  --border-color: #404040;
  --shadow-color: #0003;
  --header-bg: #2d2d2d;
  --accent-color: #2e93e8;
  --accent-color-dark: #56a9eb;
  --accent-bg-light: #2c3a4f;
  --button-bg: #333;
  --button-border: #505050;
  --button-hover-bg: #3d3d3d;
  --table-hover-bg: #313131;
  --delete-color: #ff6e72;
  --delete-hover-bg: #4a2f30;
  --disabled-bg: #3a3a3a;
  --disabled-color: #707070;
  --disabled-border: #505050;
}

* {
  box-sizing: border-box;
  font-family: var(--font-family);
  margin: 0;
  padding: 0;
  transition: background-color .3s, color .3s, border-color .3s;
}

body {
  background: var(--bg-color);
  color: var(--text-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
}

.container {
  max-width: 1400px;
  box-shadow: none;
  background: none;
  border: none;
  border-radius: 8px;
  margin: 0 auto;
  padding: 24px;
}

header {
  background: var(--header-bg);
  color: var(--text-color);
  border-bottom: 1px solid var(--border-color);
  z-index: 99;
  box-sizing: border-box;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  justify-content: space-between;
  align-items: center;
  max-width: 1400px;
  margin: 0 auto;
  padding: 24px;
  display: flex;
  position: sticky;
  top: 0;
}

.header-content {
  text-align: center;
  flex-grow: 1;
  max-width: none;
  margin: 0;
  padding: 0;
}

h1 {
  cursor: text;
  margin-bottom: 8px;
  font-size: 24px;
  font-weight: 600;
}

.theme-toggle-btn {
  background: var(--button-bg);
  border: 1px solid var(--button-border);
  cursor: pointer;
  border-radius: 50%;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  margin-left: 24px;
  font-size: 16px;
  display: flex;
  position: static;
}

.theme-toggle-btn:hover {
  border-color: var(--accent-color);
}

.controls {
  background: var(--header-bg);
  border-bottom: 1px solid var(--border-color);
  z-index: 100;
  box-sizing: border-box;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  max-width: 1400px;
  margin: 0 auto;
  padding: 16px 24px;
  display: flex;
  position: sticky;
  top: 72px;
}

.language-selector {
  color: var(--text-color);
  align-items: center;
  gap: 4px;
  font-size: 13px;
  font-weight: 600;
  display: flex;
}

.language-option {
  cursor: pointer;
  user-select: none;
  border-radius: 4px;
  padding: 4px 8px;
  transition: all .2s cubic-bezier(.1, .9, .2, 1);
}

.language-option:hover {
  background: var(--button-hover-bg);
  color: var(--accent-color);
}

.language-option.active {
  background: var(--accent-color);
  color: #fff;
  text-decoration: underline;
}

.controls-left, .controls-right {
  flex-wrap: wrap;
  align-items: center;
  gap: 12px;
  max-width: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.total-box {
  background: var(--content-bg-color);
  color: var(--accent-color-dark);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 12px 20px;
  font-size: 16px;
  font-weight: 600;
}

.btn {
  background: var(--button-bg);
  color: var(--text-color);
  border: 1px solid var(--button-border);
  cursor: pointer;
  border-radius: 4px;
  padding: 6px 12px;
  font-size: 13px;
  font-weight: 600;
  transition: all .2s cubic-bezier(.1, .9, .2, 1);
}

.btn:hover {
  background: var(--button-hover-bg);
  border-color: var(--accent-color);
  color: var(--accent-color);
}

.btn:active {
  transform: scale(.98);
}

#add-row-btn {
  background-color: var(--accent-color);
  color: #fff;
  border-color: var(--accent-color);
}

#add-row-btn:hover {
  background-color: var(--accent-color-dark);
  border-color: var(--accent-color-dark);
  color: #fff;
}

.btn-secondary, .btn-tertiary {
  background: var(--button-bg);
}

#undo-btn, #redo-btn {
  background-color: var(--button-bg);
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  padding: 4px;
  display: flex;
}

#undo-btn svg, #redo-btn svg {
  fill: var(--text-color) !important;
  stroke: none !important;
  width: 18px !important;
  height: 18px !important;
  margin: 0 !important;
  padding: 0 !important;
  display: block !important;
  overflow: visible !important;
}

#undo-btn:disabled, #redo-btn:disabled {
  background-color: var(--disabled-bg);
  color: var(--disabled-color);
  border-color: var(--disabled-border);
  cursor: not-allowed;
  opacity: .7;
}

#undo-btn:disabled:hover, #redo-btn:disabled:hover {
  background-color: var(--disabled-bg);
  border-color: var(--disabled-border);
  color: var(--disabled-color);
}

.table-container {
  padding: 0 12px 12px;
  overflow-x: auto;
}

table {
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;
  min-width: 900px;
  margin: 20px 0;
}

th, td {
  text-align: left;
  border-bottom: 1px solid var(--border-color);
  padding: 14px 16px;
}

th {
  background-color: var(--header-bg);
  color: var(--text-color-secondary);
  z-index: 10;
  font-weight: 600;
  position: sticky;
  top: 0;
}

tr:nth-child(2n) {
  background-color: #0000;
}

tr:hover {
  background-color: var(--table-hover-bg);
}

.modul-header {
  color: var(--accent-color-dark);
  cursor: text;
  font-size: 15px;
  font-weight: 600;
  background-color: var(--accent-bg-light) !important;
}

.image-column {
  vertical-align: middle;
  text-align: center;
  width: 10%;
  min-width: 120px;
  max-width: 150px;
}

.image-preview-container {
  border: 1px solid var(--border-color);
  background-color: var(--button-hover-bg);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 60px;
  margin: 0 auto;
  display: flex;
  position: relative;
  overflow: hidden;
}

.image-preview {
  object-fit: contain;
  width: 100%;
  height: 100%;
  display: block;
}

.image-controls {
  opacity: 0;
  background-color: #000000b3;
  border-radius: 4px;
  gap: 4px;
  padding: 2px;
  transition: opacity .2s ease-in-out;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.image-preview-container:hover .image-controls {
  opacity: 1;
}

.btn-image-upload, .btn-image-clear {
  background: var(--button-bg);
  cursor: pointer;
  color: #fff;
  border: none;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  padding: 4px;
  transition: background-color .2s;
  display: flex;
}

.btn-image-upload:hover, .btn-image-clear:hover {
  background-color: var(--accent-color);
}

.btn-image-clear {
  background-color: var(--delete-color);
}

.btn-image-clear:hover {
  background-color: var(--delete-hover-bg);
}

.btn-image-upload svg, .btn-image-clear svg {
  stroke: currentColor;
  fill: none;
  width: 16px;
  height: 16px;
}

.currency, .center {
  text-align: right;
  font-family: Segoe UI, Consolas, monospace;
}

.qty-input, .price-input {
  border: 1px solid var(--button-border);
  text-align: right;
  background: var(--content-bg-color);
  width: 100px;
  color: var(--text-color);
  border-radius: 4px;
  padding: 8px;
  font-size: 14px;
  transition: border-color .2s;
}

.qty-input:focus, .price-input:focus {
  border-color: var(--accent-color);
  box-shadow: 0 0 0 2px color-mix(in srgb, var(--accent-color) 30%, transparent);
  outline: none;
}

.print-text {
  display: none;
}

.note {
  color: var(--text-color-secondary);
  font-size: 13px;
  font-style: normal;
}

.total-row {
  background-color: var(--accent-bg-light);
  color: var(--accent-color-dark);
  font-weight: 600;
}

.editable-cell {
  cursor: text;
}

.delete-btn {
  color: var(--delete-color);
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  font-size: 16px;
  line-height: 1;
}

.delete-btn:hover {
  background: var(--delete-hover-bg);
}

.add-component-header-btn {
  color: var(--accent-color);
  cursor: pointer;
  background: none;
  border: none;
  padding: 2px;
  line-height: 1;
  transition: color .2s;
}

.add-component-header-btn:hover {
  color: var(--accent-color-dark);
}

.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown-toggle {
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  padding: 0;
  display: flex;
}

.dropdown-menu {
  background-color: var(--content-bg-color);
  border: 1px solid var(--border-color);
  box-shadow: 0 4px 12px var(--shadow-color);
  z-index: 101;
  border-radius: 6px;
  flex-direction: column;
  min-width: 160px;
  margin-top: 4px;
  padding: 8px 0;
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
}

.dropdown-menu.show {
  display: flex;
}

.dropdown-item {
  color: var(--text-color);
  text-align: left;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 0;
  width: 100%;
  padding: 10px 15px;
  font-size: 14px;
  font-weight: normal;
  transition: background-color .2s;
}

.dropdown-item:hover {
  background-color: var(--button-hover-bg);
  color: var(--text-color);
}

.dropdown-item#import-json-btn:hover, .dropdown-item#export-json-btn:hover, .dropdown-item#print-pdf-btn:hover {
  color: var(--accent-color);
}

footer {
  text-align: center;
  color: var(--text-color-secondary);
  background-color: #0000;
  border-top: none;
  padding: 20px;
  font-size: 12px;
}

@media print {
  body {
    -webkit-print-color-adjust: exact;
    color-adjust: exact;
    visibility: visible !important;
    color: #000 !important;
    background: #fff !important;
    margin: 0 !important;
    padding: 0 !important;
  }

  .container {
    box-shadow: none !important;
    border: none !important;
    max-width: none !important;
    padding: 0 !important;
  }

  .table-container {
    padding: 0 10px !important;
  }

  .controls, .dropdown, .theme-toggle-btn, .language-selector, .subtitle, .action-column, .image-column, .qty-input, .price-input, .delete-btn, .add-component-header-btn, .image-controls {
    visibility: hidden !important;
    display: none !important;
  }

  .print-text {
    visibility: visible !important;
    padding: 0 !important;
    display: inline !important;
  }

  .editable-cell {
    visibility: visible !important;
    color: #000 !important;
    background-color: #0000 !important;
    font-weight: normal !important;
    display: table-cell !important;
  }

  #rab-title {
    text-align: center;
    color: #000 !important;
    padding: 10px 0 !important;
    font-size: 20px !important;
    font-weight: bold !important;
  }

  header {
    position: static;
    box-shadow: none !important;
    background: #fff !important;
    border-bottom: 2px solid #000 !important;
    margin: 0 !important;
    padding: 0 !important;
  }

  #rab-table {
    border-collapse: collapse !important;
    border-spacing: 0 !important;
    min-width: 100% !important;
    margin: 0 auto !important;
  }

  #rab-table thead th {
    color: #000 !important;
    background-color: #e0e0e0 !important;
  }

  .modul-header, .total-row {
    color: #000 !important;
    background-color: #ccc !important;
    font-weight: bold !important;
  }

  .center-print {
    text-align: center !important;
  }

  .currency {
    text-align: right !important;
    font-family: Segoe UI, Arial, sans-serif !important;
  }

  footer {
    display: none !important;
  }

  #rab-table {
    border-collapse: collapse !important;
    border-spacing: 0 !important;
    min-width: 100% !important;
    margin: 0 auto !important;
    font-size: 9pt !important;
  }

  th, td {
    visibility: visible !important;
    background-color: #0000 !important;
    border: 1px solid #000 !important;
    padding: 5px 8px !important;
    display: table-cell !important;
  }
}

@media (width <= 1200px) {
  .container, header, .controls {
    padding-left: 16px;
    padding-right: 16px;
  }
}

@media (width <= 992px) {
  h1 {
    font-size: 20px;
  }

  .controls {
    top: 68px;
  }

  .total-box {
    padding: 10px 16px;
    font-size: 14px;
  }

  .btn {
    padding: 5px 10px;
    font-size: 12px;
  }
}

@media (width <= 768px) {
  .container {
    padding: 8px;
  }

  header, .controls {
    border-radius: 0;
    padding-left: 12px;
    padding-right: 12px;
  }

  .controls {
    flex-direction: column;
    align-items: stretch;
    top: 68px;
  }

  .controls-left, .controls-right {
    justify-content: center;
    width: 100%;
  }

  .total-box {
    text-align: center;
    width: 100%;
  }

  table, thead, tbody, th, td, tr {
    display: block;
  }

  table {
    border: none;
    min-width: 100%;
  }

  thead {
    display: none;
  }

  tr {
    border: 1px solid var(--border-color);
    background: var(--content-bg-color);
    box-shadow: 0 2px 4px var(--shadow-color);
    border-radius: 6px;
    margin-bottom: 15px;
  }

  .modul-header {
    text-align: center;
    border-radius: 6px;
    padding: 12px 15px;
  }

  .modul-header td {
    border: none;
  }

  td {
    border: none;
    border-bottom: 1px solid var(--border-color);
    text-align: right;
    white-space: normal;
    justify-content: space-between;
    align-items: center;
    padding-left: 50%;
    display: flex;
    position: relative;
  }

  td:last-child {
    border-bottom: none;
  }

  td:before {
    content: attr(data-label);
    white-space: nowrap;
    text-align: left;
    width: 45%;
    color: var(--text-color-secondary);
    padding-right: 10px;
    font-weight: bold;
    position: absolute;
    left: 10px;
  }

  #rab-tbody td:first-of-type:before {
    content: "Modul";
  }

  #rab-tbody td:nth-of-type(2):before {
    content: "Kategori";
  }

  #rab-tbody td:nth-of-type(3):before {
    content: "Komponen";
  }

  #rab-tbody td:nth-of-type(4):before {
    content: "Jumlah";
  }

  #rab-tbody td:nth-of-type(5):before {
    content: "Satuan";
  }

  #rab-tbody td:nth-of-type(6):before {
    content: "Harga (IDR)";
  }

  #rab-tbody td:nth-of-type(7):before {
    content: "Subtotal (IDR)";
  }

  #rab-tbody td:nth-of-type(8):before {
    content: "Keterangan";
  }

  #rab-tbody td:nth-of-type(9):before {
    content: "Gambar";
  }

  #rab-tbody td:nth-of-type(10):before {
    content: "Aksi";
  }

  .qty-input, .price-input {
    width: 80px;
  }

  .editable-cell {
    text-align: right;
  }

  .image-column .image-preview-container {
    margin: 0;
  }

  .action-column {
    justify-content: flex-end;
  }

  .total-row td {
    justify-content: center;
    padding-left: 15px;
    font-size: 16px;
  }

  .total-row td:before {
    display: none;
  }
}
